export default {
    data() {
        return {
            status: 1, img:[require('../../assets/img/fail.png'),require('../../assets/img/result.png')],
            time:3
        }
    },
    components: {
        
    },
    computed: {
        
    },
    created(){
        if(this.status == 1){
            let a = setInterval(()=>{
                this.time --
                if(this.time == 0){
                    clearInterval(a)
                    this.$router.push({path:'/My'})
                }
            },1000)
        }
    },
    methods: {
        again(){
            this.$router.go(-1)
        }
    },
}



